body {
  color: #e6e6e6;
  background: #1f1f1f;
  margin: 0;
  padding: 40px;
  font-family: Inter, sans-serif;
}

.container {
  background: #2a2a2a;
  border-radius: 14px;
  max-width: 900px;
  margin: 0 auto;
  padding: 30px;
  box-shadow: 0 4px 20px #0006;
}

h1, h2 {
  text-align: center;
  letter-spacing: -.3px;
  color: #f0f0f0;
  margin-bottom: 25px;
  font-weight: 700;
}

button {
  cursor: pointer;
  border: none;
  border-radius: 8px;
  padding: 10px 18px;
  font-size: 14px;
  font-weight: 600;
  transition: all .2s;
}

#get-students-btn {
  color: #1f1f1f;
  background: #d4a017;
  margin: 0 auto 25px;
  display: block;
}

#get-students-btn:hover {
  background: #bb8c14;
}

.update-btn {
  color: #e6e6e6;
  background: #3a3a3a;
}

.update-btn:hover {
  background: #4a4a4a;
}

.delete-btn {
  color: #fff;
  background: #d64545;
}

.delete-btn:hover {
  background: #b83a3a;
}

table {
  border-collapse: collapse;
  background: #2f2f2f;
  border-radius: 12px;
  width: 100%;
  margin-top: 20px;
  overflow: hidden;
}

thead {
  color: #f0f0f0;
  background: #3a3a3a;
}

th, td {
  border-bottom: 1px solid #444;
  padding: 14px 16px;
  font-size: 14px;
}

tbody tr:hover {
  background: #383838;
}

form {
  gap: 14px;
  margin-top: 25px;
  display: grid;
}

input[type="text"], input[type="number"], input[type="email"] {
  color: #e6e6e6;
  background: #1f1f1f;
  border: 1px solid #555;
  border-radius: 8px;
  padding: 12px;
  font-size: 14px;
  transition: all .2s;
}

input:focus {
  border-color: #d4a017;
  outline: none;
}

#add-student-form button {
  color: #1f1f1f;
  background: #d4a017;
}

#add-student-form button:hover {
  background: #bb8c14;
}

.modal {
  backdrop-filter: blur(4px);
  z-index: 1000;
  background: #0000008c;
  display: none;
  position: fixed;
  inset: 0;
}

.modal-content {
  background: #2a2a2a;
  border-radius: 14px;
  width: 380px;
  margin: 10% auto;
  padding: 28px;
  animation: .25s ease-out fadeIn;
  box-shadow: 0 10px 30px #00000080;
}

.close {
  float: right;
  cursor: pointer;
  color: #aaa;
  font-size: 24px;
}

.close:hover {
  color: #fff;
}

#update-student-form button {
  color: #1f1f1f;
  background: #d4a017;
}

#update-student-form button:hover {
  background: #bb8c14;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-8px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}
